<template>
    <main id="cash"
          class="container">
        <section>
            <header class="head-title"></header>
            <p><span class="money">{{amount | showNum(amount)}}</span></p>
            <p v-if="bindState">{{person}}</p>
            <p v-if="bindState">{{bank | hide(bank)}}</p>
            <p v-if="bindState">{{mobile | hide(mobile)}}</p>
        </section>
        <section>
            <el-form :label-position="'top'"
                     label-width="80px"
                     :model="cashForm"
                     ref="cashForm"
                     v-if="bindState">
                <el-form-item label="">
                    <el-input-number :min="1"
                                     :max="maxMoney"
                                     v-model="cashForm.cashAmount"
                                     class="item"></el-input-number>
                    <!--<p style="margin:0;"><span class="money">{{(amount*(1-fee)).toFixed(0) | showNum(amount*(1-fee))}}</span></p>-->
                    <p class="tip"
                       style="margin:0;">
                        <el-button type="text"
                                   @click="feeVis = !feeVis">
                            
                        </el-button><span class="money">
                            {{serverMoney}} 
                        </span>
                    </p>
                </el-form-item>
                <el-form-item label="">
                    <el-input :maxlength="6" @keyup.enter="submitCash('cashForm')" v-model="cashForm.code" class="item">
                        <el-button :style="{cursor:(codeBtn.isAjax || codeBtn.text.includes('') ? 'not-allowed' : 'pointer')}" slot="append" @click="getCode()" :disabled="codeBtn.isAjax || codeBtn.text.includes('')">{{codeBtn.text}}</el-button>
                    </el-input>
                </el-form-item>
                <el-button style="margin-bottom:20px;" type="primary" @click="submitCash('cashForm')" :loading="submitBtn"></el-button>
            </el-form>
            <div v-if="!bindState">
                <p> <el-button @click="toBind"></el-button></p>
                <p class="tip"></p>
            </div>
        </section>
        <section class="tips">
            <header></header>
            <p class="subtitle"></p>
            <p>13</p>
            <p>248</p>
            <p class="subtitle"></p>
            <p></p>
            <p class="subtitle"></p>
            <p></p>
        </section>
        <el-dialog :visible.sync="feeVis"
                   width="40%"
                   title="">
            <el-table :data="feeArr"
                      size="mini"
                      stripe>
                <el-table-column label=""
                                 min-width="240">
                    <template slot-scope="scope">
                        <span>{{showServerMoney(scope)}}</span>
                    </template>
                </el-table-column>
                <el-table-column label="/"
                                 min-width="240">
                    <template slot-scope="scope">
                        <span class="money">{{''+scope.row.rate}}</span>
                    </template>
                </el-table-column>
            </el-table>
        </el-dialog>
    </main>
</template>
<script>
export default{
  data () {
    return {
      bindState: false,
      amount: 0,
      fee: 0.01,
      person: '',
      bank: '',
      mobile: '',
      cashForm: {
        cashAmount: '',
        code: ''
      },
      maxMoney: 0,
      serverMoney: '10.00',
      feeArr: [],
      codeBtn: {
        text: '',
        isAjax: false
      },
      submitBtn: false,
      feeVis: false
    }
  },
  filters: {
    showNum (value) {
      value = value === undefined ? 0 : value
      return value.toString().split('').reverse().join('').replace(/(\d{3})/g, '$1,').replace(/,$/, '').split('').reverse().join('')
    },
    hide (value) {
      value = value === undefined ? 0 : value
      return value.substring(0, 3) + '******' + value.substring(value.length - 2, value.length)
    }
  },
  watch: {
    cashForm: {
      handler (curVal, oldVal) {
        this.getThisServerMoney(oldVal.cashAmount)
      },
      deep: true
    }
  },
  methods: {
    toBind () {
      this.$parent.addTab('/')
      this.$router.push('/Main/WCreate')
    },
    submitCash (formName) {
      const that = this
      const __url = that.$store.state.total.wallet.child.getCash[1]
      if (__url === '') return false
      this.submitBtn = !that.submitBtn
      if (that.cashForm.cashAmount < 0.01) {
        that.$message.warning({
          message: '0.01'
        })
        that.submitBtn = !that.submitBtn
        return false
      }
      if (that.cashForm.code === '') {
        that.$message.warning({
          message: ''
        })
        that.submitBtn = !that.submitBtn
        return false
      }
      that.$ajax({
        url: __url,
        method: 'post',
        data: {
          money: that.cashForm.cashAmount,
          phone: that.mobile,
          poundage: that.serverMoney >> 0,
          code: that.cashForm.code
        }
      }).then(res => {
        that.submitBtn = !that.submitBtn
        const _data = res.data
        if ((_data.errcode >> 0) === 0) {
          that.$notify.success({
            title: ' ' + that.cashForm.cashAmount + ' ',
            message: ''
          })
        } else if ((_data.errcode >> 0) === 1000 || (_data.errcode >> 0) === 1001) {
          that.$parent.overTime()
        } else {
          that.$message.warning(_data.msg)
        }
      }, res => {
        let that = this
        that.submitBtn = !that.submitBtn
        that.$message.warning('')
      })
    },
    showServerMoney (scope) {
      const that = this
      const _amount = scope.row.amount
      if (_amount === that.feeArr[0].amount) {
        return (_amount - 1) + ''
      } else if ((_amount - 0) < (that.feeArr[that.feeArr.length - 1].amount - 0)) {
        return that.feeArr[scope.$index - 1].amount + '-' + (_amount - 1)
      } else {
        return that.feeArr[scope.$index - 1].amount - 1 + ''
      }
    },
    getServeFeeList () {
      const that = this
      that.$ajax({
        url: that.$store.state.getCashFee
      }).then(res => {
        const _data = res.data
        if ((_data.error >> 0) === 0) {
          _data.datArr.forEach((e, i) => {
            that.feeArr.push({
              amount: e.day,
              rate: e.rate
            })
          })
        } else {
          that.$message.warning('')
        }
      }, res => {
        that.$message.warning('')
      })
    },
    getThisServerMoney (val) {
      let that = this
      try {
        if (that.maxMoney - 0 === that.amount - 0) {
          for (let i = 0; i < that.feeArr.length; i++) {
            if (that.maxMoney < (that.feeArr[i].amount - 0)) {
              that.maxMoney -= that.feeArr[i].rate
            }
          }
          if (that.maxMoney > (that.feeArr[that.feeArr.length - 1].amount - 0)) {
            that.maxMoney -= that.feeArr[that.feeArr.length - 1].rate
          }
        }
        for (let i = 0; i < that.feeArr.length; i++) {
          if (val < (that.feeArr[i].amount - 0)) {
            that.serverMoney = that.feeArr[i].rate
            return false
          }
        }
        that.serverMoney = that.feeArr[that.feeArr.length - 1].rate
        return false
      } catch (error) {}
    },
    getCode () {
      let that = this
      that.codeBtn.isAjax = !that.codeBtn.isAjax
      const __url = that.$store.state.code.bindCardCode
      if (__url === '') return false
      if (!/^1[34578]\d{9}$/.test(that.mobile)) {
        that.$message.warning('')
        return false
      }
      that.$ajax({
        url: __url,
        params: {
          mobi: that.mobile
        }
      }).then(res => {
        const _data = res.data
        that.codeBtn.isAjax = false
        let _t = 60
        const timer = setInterval(() => {
          that.codeBtn.text = ' ' + _t + 's'
          _t--
          if (_t <= 0) {
            that.codeBtn.text = ''
            clearInterval(timer)
          }
        }, 1000)
        if ((_data.errcode >> 0) === 0) {
          that.$notify.success({
            title: '',
            message: '' + that.mobile
          })
        } else if ((_data.errcode >> 0) === 1000 || (_data.errcode >> 0) === 1001) {
          that.$parent.overTime()
        } else {
          that.$message.warning(_data.msg)
        }
      }, res => {
        that.codeBtn.isAjax = true
        that.$message.warning('')
      })
    }
  },
  mounted () {
    const that = this
    const __url = that.$store.state.total.wallet.child.info[1]
    if (__url === '') return false
    that.getServeFeeList()
    that.$ajax({
      url: __url
    }).then(res => {
      const _data = res.data
      if ((_data.errcode >> 0) === 0) {
        const dat = _data.data
        that.amount = dat.balance === null ? 0 : (dat.balance - 0).toFixed(2)
        that.maxMoney = that.amount - 0
        if (dat.bank_card === null || dat.bank_name === null || dat.phone === null) {
          that.bindState = false
        } else {
          that.bindState = true
          that.bank = dat.bank_card
          that.person = dat.bank_name
          that.mobile = dat.phone
        }
      } else if ((_data.errcode >> 0) === 1000 || (_data.errcode >> 0) === 1001) {
        that.$parent.overTime()
      } else {
        that.$message.warning(_data.msg)
      }
    }, res => {
      that.$message.warning('')
    })
  }
}
</script>
<style>
    #cash .money{
        color:#FF4949;
    }
    #cash .tip{
        font-size:12px;
    }
    #cash section{
        color:#99a9bf;
        padding:20px 20px 0 20px;
        overflow: hidden;
        margin:2px 2px 20px 2px;
        background:#fff;
        -webkit-box-shadow: 0 0 10px 0 #ddd;
        -moz-box-shadow: 0 0 10px 0 #ddd;
        box-shadow: 0 0 10px 0 #ddd;
    }
    #cash section p{
        text-align:left;
        margin:10px 0;
    }
    #cash section p:last-of-type{
        margin-bottom:20px;
    }
    #cash .item{
        width:300px;
    }
    #cash .tips{
        padding-top:0;
        font-size:14px;
    }
    #cash .tips header{
        border-left:8px solid #ff4949;
        background: #c0ccda;
        margin:0 -20px;
        padding:10px 12px;
        color:#fff;
        font-size:18px;
    }
    #cash label,#cash .tips .subtitle{
        color:#1F2D3D;
        font-size:16px;
    }
</style>



