<template>
    <main id="w_create" class="container">
        <header class="head-title" v-if="!bindState"></header>
        <header class="head-title" v-if="bindState"></header>
        <div class="bank-info" v-if="bindState">
            <span></span>
            <span>{{info.name}}</span>
            <span>{{info.card | hide(info.card)}}</span>
            <span>{{info.mobile | hide(info.mobile)}}</span>
        </div>
        <div class="form-container">
            <el-form label-positon="left" ref="bindCard" :model="bindCard" :rules="rules" label-width="100px">
                <el-form-item prop="name" label="">
                    <el-input v-model="bindCard.name" placeholder="" :maxlength="30"></el-input>
                </el-form-item>
                <el-form-item prop="card" label="">
                    <el-input v-model="bindCard.card" placeholder="" :maxlength="19"></el-input>
                </el-form-item>
                <el-form-item prop="place" label="">
                    <el-input v-model="bindCard.place" placeholder=""></el-input>
                </el-form-item>
                <el-form-item prop="mob" label="">
                    <el-input v-model="bindCard.mob" placeholder="" :maxlength="11"></el-input>
                </el-form-item>
                <el-form-item prop="code" label="">
                    <el-input v-model="bindCard.code" placeholder="" :maxlength="6">
                            <el-button :style="{cursor:(codeBtn.isAjax || codeBtn.text.includes('') ? 'not-allowed' : 'pointer')}" slot="append" @click="getCode()" :disabled="codeBtn.isAjax || codeBtn.text.includes('')" :loading="codeBtn.isAjax">{{codeBtn.text}}</el-button>
                    </el-input>
                </el-form-item>
                <el-form-item class="text-center">
                    <el-button type="primary" @click="bind('bindCard')"> </el-button>
                </el-form-item>
            </el-form>
        </div>
        <div class="tip text-center" v-if="bindState"></div>
    </main>
</template>
<script>export default{
  data () {
    let checkBank = (rule, value, callback) => {
      value = value.replace(/\s/g, '')
      if (value === '') {
        callback(new Error(''))
      } else if (value.length !== 16 && value.length !== 19) {
        callback(new Error(''))
      } else {
        callback()
      }
    }
    let checkPhone = (rule, value, callback) => {
      value = value.replace(/\s/g, '')
      if (!/^1[34578]\d{9}$/.test(value)) {
        callback(new Error(''))
      } else {
        callback()
      }
    }
    return {
      bindState: false,
      bindCard: {
        name: '',
        card: '',
        place: '',
        mob: '',
        code: ''
      },
      info: {
        name: '',
        card: '',
        mobile: ''
      },
      codeBtn: {
        isAjax: false,
        text: ''
      },
      rules: {
        name: [
          {required: true, message: '', trigger: 'blur'},
          {min: 2, max: 30, message: '2-30', trigger: 'blur'}
        ],
        card: [
          {required: true, validator: checkBank, trigger: 'blur'}
        ],
        place: [
          {required: true, message: '', trigger: 'blur'},
          {min: 6, message: '6', trigger: 'blur'}
        ],
        mob: [
          {required: true, validator: checkPhone, trigger: 'blur'}
        ],
        code: [
          {required: true, message: '', trigger: 'blur'}
        ]
      }
    }
  },
  methods: {
    getCode () {
      const that = this
      const __url = that.$store.state.code.bindCardCode
      if (__url === '') return false
      if (!/^1[34578]\d{9}$/.test(that.bindCard.mob)) {
        that.$message.warning('')
        return false
      }
      that.codeBtn.isAjax = true
      that.$ajax({
        url: __url,
        params: {
          mobi: that.bindCard.mob
        }
      }).then(res => {
        const _data = res.data
        that.codeBtn.isAjax = false
        let _t = 60
        const timer = setInterval(() => {
          that.codeBtn.text = ' ' + _t + 's'
          _t--
          if (_t <= 0) {
            that.codeBtn.text = ''
            clearInterval(timer)
          }
        }, 1000)
        if ((_data.errcode >> 0) === 0) {
          that.$notify.success({
            title: '',
            message: '' + that.bindCard.mob
          })
        } else if ((_data.errcode >> 0) === 1000 || (_data.errcode >> 0) === 1001) {
          that.$parent.overTime()
        } else {
          that.$message.warning(_data.msg)
        }
      }, res => {
        that.codeBtn.isAjax = true
        that.$message.warning('')
      })
    },
    bind (formName) {
      const that = this
      const __url = that.$store.state.total.wallet.child.update[1]
      if (__url === '') return false
      that.$refs[formName].validate((valid) => {
        if (valid) {
          that.bindCard.name = that.bindCard.name.replace(/\s/g, '')
          that.bindCard.card = that.bindCard.card.replace(/\s/g, '')
          that.bindCard.place = that.bindCard.place.replace(/\s/g, '')
          that.bindCard.mob = that.bindCard.mob.replace(/\s/g, '')
          that.$ajax({
            url: __url,
            params: {
              name: that.bindCard.name.replace(/\s/g, ''),
              bank_name: that.bindCard.name.replace(/\s/g, ''),
              bank_card: that.bindCard.card.replace(/\s/g, ''),
              phone: that.bindCard.mob.replace(/\s/g, ''),
              code: that.bindCard.code.replace(/\s/g, '')
            }
          }).then(res => {
            const _data = res.data
            if ((_data.errcode >> 0) === 0) {
              that.$message.success('')
              that.$parent.addTab('')
              that.$router.push('/Main/WCash')
            } else if ((_data.errcode >> 0) === 1000 || (_data.errcode >> 0) === 1001) {
              that.$parent.overTime()
            } else {
              that.$message.warning(_data.msg)
            }
          }, res => {
            that.$message.warning('')
          })
        }
      })
    },
    unbind (forName) {
      /*
              that.$refs[formName].validate((valid) => {
                  if(valid){
                      that.bindCard.name = that.bindCard.name.replace(/\s/g,'');
                      that.bindCard.card = that.bindCard.card.replace(/\s/g,'');
                      that.bindCard.place = that.bindCard.place.replace(/\s/g,'');
                      that.bindCard.mob = that.bindCard.mob.replace(/\s/g,'');
                      that.$ajax({
                          url:that.$store.state.total.wallet.update[1],
                          params:{
                              name:that.bindCard.name.replace(/\s/g,''),
                              bank_name:that.bindCard.name.replace(/\s/g,''),
                              bank_card:that.bindCard.card.replace(/\s/g,''),
                              phone:that.bindCard.mob.replace(/\s/g,'')
                          }
                      }).then(res=>{
                          const _data = res.data;
                          if((_data.errcode>>0)===0){
                              that.$message.success('');
                          }else if((_data.errcode>>0)===1000||(_data.errcode>>0)===1001){
                              that.$parent.overTime();
                          } else {
                              that.$message.warning(_data.msg);
                          }
                      },res=>{
                          that.$message.warning('');
                      });
                  }
              });
              */
    }
  },
  filters: {
    hide (value) {
      return value.substring(0, 3) + '******' + value.substring(value.length - 2, value.length)
    }
  },
  mounted () {
    const that = this
    const __url = that.$store.state.total.wallet.child.info[1]
    if (__url === '') return false
    that.$ajax({
      url: __url
    }).then(res => {
      const _data = res.data
      if ((_data.errcode >> 0) === 0) {
        const dat = _data.data
        that.amount = dat.balance === null ? 0 : (dat.balance >> 0)
        if (dat.bank_card === null || dat.bank_name === null || dat.phone === null) {
          that.bindState = false
        } else {
          that.bindState = true
          that.info = {
            card: dat.bank_card,
            name: dat.bank_name,
            mobile: dat.phone
          }
        }
      } else if ((_data.errcode >> 0) === 1000 || (_data.errcode >> 0) === 1001) {
        that.$parent.overTime()
      } else {
        that.$message.warning(_data.msg)
      }
    }, res => {
      that.$message.warning('')
    })
  }
}
</script>
<style>
    #w_create{
        background:#fff;
        -webkit-box-shadow: 0 0 10px 0 #ddd;
        -moz-box-shadow: 0 0 10px 0 #ddd;
        box-shadow: 0 0 10px 0 #ddd;
    }
    #w_create .form-container{
        width:40%;
        min-width:468px;
        /*margin:0 auto;*/
    }
    #w_create .tip{
        font-size:12px;
        color:#8391a5;
    }
    #w_create .bank-info{
        padding:10px 0 10px 40px;
        color:#99a9bf;
        font-size:14px;
    }
    #w_create .bank-info span{
        margin-left:10px;
        line-height:24px;
    }
</style>



